/* eslint-disable no-fallthrough */
import React from 'react'

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faLocationDot } from '@fortawesome/free-solid-svg-icons'
import { faRocket } from '@fortawesome/free-solid-svg-icons'
import { faShuttleSpace } from '@fortawesome/free-solid-svg-icons'
import { faHelicopter } from '@fortawesome/free-solid-svg-icons'

export default function ButtonMission(props) {

    let timeAnim = 500
    let iconFA = ""
    // eslint-disable-next-line default-case
    switch (props.icon) {
        case 'faRocket':
            iconFA = faRocket;
            break;
        case 'faShuttleSpace':
            iconFA = faShuttleSpace;
            break;
        case 'faHelicopter':
            iconFA = faHelicopter;
            break;
        case 'faLocationDot':
            iconFA = faLocationDot;
            break;
    }

    const pickOption = () => {
        animationFunc()
        window.setTimeout(changeToggleFunc, timeAnim);
    }

    const changeToggleFunc = () => {
        props.changeToggle(false)
        props.changeNextToggle(props.icon)
    }

    const animationFunc = () => {
        const img_test = document.getElementsByClassName(props.index + " square")
        const temp = Object.values(img_test);

        temp.forEach(link => {
            link.animate(
                aliceTumbling,
                aliceTiming
            )
        });
    }

    let aliceTumbling = [
        { transform: 'scale(1)', color: '#000', opacity: '1' }, //translate3D(-50%, -50%, 0)
        // { color: '#431236', offset: 0.3},
        { transform: 'scale(50)', color: '#000', opacity: '1' } //translate3D(-50%, -50%, 0)
    ];

    let aliceTiming = {
        duration: timeAnim,
        iterations: 1,
        easing: "ease-in",
        fill: "forwards"
    }

    return (
        <button
            className="button_icon"
            onClick={pickOption}
        >
            <div className={props.index + " square"}>
                <FontAwesomeIcon className={props.icon} icon={iconFA} />
            </div>
        </button>
    )
}