import './App.css';

import React from 'react'
import FrontPage from './Components/FrontPage/FrontPage'
import Entree from './Components/Entree/Entree'
import Mission from './Components/Mission/MissionMain'
import MissionSpecifique from './Components/Mission/MissionSpecifique';
import { useState } from 'react';

function App() {

  const [toggleFront, setToggleFront] = useState(false);
  const [toggleEntree, setToggleEntree] = useState(false);
  const [toggleMission, setToggleMission] = useState(true);
  // const [toggleMissionOne, setToggleMissionOne] = useState("faShuttleSpace");
  const [toggleMissionOne, setToggleMissionOne] = useState(false);

  return (
    <>
      {toggleFront && <FrontPage changeToggle={setToggleFront} changeNextToggle={setToggleEntree} />}
      {toggleEntree ? <Entree changeToggle={setToggleEntree} changeNextToggle={setToggleMission} /> : ""}
      {toggleMission ? <Mission changeToggle={setToggleMission} changeNextToggle={setToggleMissionOne} /> : ""}
      {toggleMissionOne ? <MissionSpecifique icon={toggleMissionOne} /> : ""}
    </>
  )
}

export default App;