import React from 'react'
import './Entree.css'
import { useState } from 'react';

export default function Entree(props) {

    const [pass, setPass] = useState("");
    const [passValid, setPassValid] = useState(false);
    let letterMove = true;

    const changeInput = e => {
        setPass(e)
        console.log(e)
        // if (pass === "") {
        if (e === "AA") {
            setPassValid(true)
            // letterMove = false
            // orderLetter()

            loadAnimation()
        }
    }

    const enterNext = () => {
        if (passValid) {
            props.changeToggle(false)
            props.changeNextToggle(true)
        }
    }

    // const img_test = document.getElementsByTagName("body");
    const lettreEnMouvement = document.getElementsByClassName("letter_center")
    const temp = Object.values(lettreEnMouvement);

    // console.log(lettreEnMouvement)
    // console.log(temp)

    let aliceTumbling = [
        { color: '#000' }, //translate3D(-50%, -50%, 0)
        // { color: '#431236', offset: 0.3},
        { transform: 'rotate(0deg)', color: 'red' } //translate3D(-50%, -50%, 0)
    ];

    // console.log(aliceTumbling)
    let aliceTiming = {
        duration: 500,  //TIME
        iterations: 1,
        easing: "ease-in-out",
        fill: "forwards"
    }

    const loadAnimation = () => {
        let wordLength = temp.length
        // console.log("wordLength =" + wordLength)
        temp.forEach((link, index) => {
            let spreadLength = (wordLength - 1) * 7
            let degLettre = -spreadLength / 2 + (7) * index
            // degLettre = 0
            // console.log("======")
            // console.log(index)
            // console.log(temp[index])
            // console.log(spreadLength)
            // console.log(degLettre + 'deg')
            setTimeout(function () {
                link.animate(
                    [
                        { color: '#000' }, //translate3D(-50%, -50%, 0)
                        // { color: '#431236', offset: 0.3},
                        { transform: 'rotate(' + degLettre + 'deg)', color: 'black', top: '24%', height: '52%', fontSize: '2em' } //translate3D(-50%, -50%, 0)
                    ],
                    aliceTiming
                )
            }

                , index * 50) //TIME
        });
    }

    const word = "Bienvenue Agent !"
    // const word = "Je t'aime p'tit chou <3"
    let list_letter = ["B", "B", "C", "D", "E", "F", "G", "H", "I"];
    list_letter = word.split('')
    // console.log(list_letter)

    let roundLetter = []
    let N = list_letter.length;

    let j = 0
    if (letterMove) {
        for (let i = 0; i < N; i++) {
            if (j > 10) { j = 0 }
            let classNameStr = "letter_center length" + (i % 6) + " rotate" + (j + 1)
            // console.log("lettre=__" + list_letter[i] + "__rotate=__" + (j + 1) + "__length=__" + (i % 6))
            roundLetter.push(<div key={i} className={classNameStr} style={{

                // transform: true ? 'rotate(180deg)' : '',
                // transition: 'transform 150ms ease', // smooth transition
                // transform: [{ rotate: '90deg' }]

            }}>{list_letter[i]}</div>) //style={divStyle}
            j++
        }
    }


    const orderLetter = () => {
        // roundLetter = []
        // console.log(list_letter)
        // console.log(roundLetter)

        // for (let i = 0; i < N; i++) {
        // if (j > 10) { j = 0 }
        // let classNameStr = "letter_center length" + (i % 6) + ' ' + " rotate" + (j + 1)
        // console.log("lettre=__" + list_letter[i] + "__rotate=__" + (j + 1) + "__length=__" + (i % 6))
        // roundLetter.push(<div key={i} className={classNameStr} style={{

        // transform: true ? 'rotate(180deg)' : '',
        // transition: 'transform 150ms ease', // smooth transition
        // transform: [{ rotate: '90deg' }] 


        // }}>{list_letter[i]}</div>) //style={divStyle}
        // j++
        // }

    }

    return (
        <div className="container_all_entree">
            {/* <h1>Hi you !</h1> */}

            <div id="letter_anim">
                {letterMove ? roundLetter : ""}
            </div>


            {/* <div className="square_test"></div> */}
            {/* <div className="square_test2"></div> */}
            {/* <div className="square_test3"></div> */}
            {/* <div className="square_test4"></div> */}
            {!passValid ?
                <input
                    autoComplete="off"
                    placeholder="- ? -"
                    className="input_center"
                    type="text"
                    name="code"
                    id="code"
                    onInput={e => changeInput(e.target.value)}
                    value={pass} />
                :
                <>
                    <button
                        className="button_center"
                        onClick={enterNext}
                    >
                        Entrer
                    </button>
                </>
            }

            {/* <h1>{pass}</h1> */}
            {/* <h1>{pass}</h1> */}

            {/* <button>Valider</button> */}

        </div>
    )
}
