/* eslint-disable default-case */
import React from 'react'
import './Mission.css'
import ButtonMission from './ButtonMission'
// import MissionSpecifique from './MissionSpecifique'
// import { useState } from 'react';

export default function Mission(props) {

    // let iconList = ["faRocket", "faShuttleSpace", "faHelicopter", "faLocationDot"]
    let iconList = ["faRocket", "faHelicopter", "faLocationDot"]
    let iconHTML = []
    iconList.forEach((icon, index) => {
        iconHTML.push(<ButtonMission key={index} index={index} icon={icon} changeToggle={props.changeToggle} changeNextToggle={props.changeNextToggle}>
        </ButtonMission>)
    })

    // let mission_specifique = false;

    // switch (iconName) {
    //     case 'faRocket':
    //         // iconFA = faRocket;
    //         break;
    //     case 'faShuttleSpace':
    //         // iconFA = faShuttleSpace;
    //         break;
    //     case 'faHelicopter':
    //         // iconFA = faHelicopter;
    //         break;
    //     case 'faLocationDot':
    //         // iconFA = faLocationDot;
    //         break;
    // }



    return (
        <div className="outside_container">
            <div className="container_main">

                <div className="ensemble_dessin">
                    <div className="galerie_icon">
                        {iconHTML}
                    </div>
                    <div className="floor">
                    </div>
                </div>

                <h1>Mission</h1>

            </div>
        </div>
    )
}

